import uCharts from '@/components/u-charts/u-charts.js';
import  { isJSON } from '@/common/checker.js';
var _self;
var canvaRadar=null;
export default {
	data() {
		return {
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			textarea:''
		}
	},
	onLoad() {
		_self = this;











		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		getServerData(){
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data:{
				},
				success: function(res) {
					console.log(res.data.data)
					let Radar={categories:[],series:[]};
					Radar.categories=res.data.data.Radar.categories;
					Radar.series=res.data.data.Radar.series;
					_self.textarea = JSON.stringify(res.data.data.Radar);
					_self.showRadar("canvasRadar",Radar);
				},
				fail: () => {
					_self.tips="网络错误，小程序端请检查合法域名";
				},
			});
		},
		showRadar(canvasId,chartData){
			canvaRadar=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'radar',
				fontSize:11,
				padding:[15,15,0,15],
				legend:{
					show:true,
					padding:5,
					lineHeight:11,
					margin:0,
				},
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				animation: true,
				dataLabel: true,
				categories: chartData.categories,
				series: chartData.series,
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					radar: {
						max: 200//雷达数值的最大值
					}
				}
			});
		},
		touchRadar(e){
			canvaRadar.touchLegend(e);
			canvaRadar.showToolTip(e, {
				format: function (item) {
					return item.name + ':' + item.data 
				}
			});
		},
		changeData(){
			if(isJSON(_self.textarea)){
				let newdata=JSON.parse(_self.textarea);
				canvaRadar.updateData({
					series: newdata.series,
					categories: newdata.categories
				});
			}else{
				uni.showToast({
					title:'数据格式错误',
					image:'../../../static/images/alert-warning.png'
				})
			}
		}
	}
}
